import { Component, h, Host, Prop } from '@stencil/core';
import { TitleTag, TitleVariant, TitleColor, TitleSize } from './title.types';

/**
 * The title component.
 */
@Component({
  tag: 'bs-title',
  styleUrl: 'title.scss',
  shadow: true,
})
export class BsTitle {
  // add camelcase?

  /**
   * To customize the text
   */
  @Prop() text: string;

  /**
   * To set the title tag
   */
  @Prop() tag: TitleTag = TitleTag.H2;

  /**
   * To set the color
   */
  @Prop() color: TitleColor = TitleColor.Undefined;

  /**
   * To set the title variant
   */
  @Prop() variant: TitleVariant = TitleVariant.None;

  /**
   * To set the title size
   */
  @Prop() size?: TitleSize = TitleSize.Head1;

  render() {
    const Tag = this.tag;
    const titleVariant =
      this.variant && this.variant !== 'undefined' ? `bs-title--${this.variant}` : '';
    const titleColor = Object.values(TitleColor).includes(this.color)
      ? `text-color-${this.color}`
      : `text-color-${TitleColor.Undefined}`;
    const fontSize = Object.values(TitleSize).includes(this.size)
      ? `font-${this.size}`
      : `font-${TitleSize.Head1}`;

    return (
      <Host>
        <Tag class={`${titleColor} bs-title ${titleVariant} ${fontSize}`}>{this.text}</Tag>
      </Host>
    );
  }
}
