$c: '.bs-title';
#{$c} {
  margin: 0;

  &--dash {
    position: relative;
    padding-top: rem(18);

    &:before {
      position: absolute;
      top: 0;
      left: 0;

      width: rem(18);
      height: 3px;

      content: '';

      background: $gray-900;
      border-radius: 2px;
    }

    &.text-color-white {
      &:before {
        background: $white;
      }
    }
  }

  &--slash {
    margin-left: 1.1em;

    &:before {
      position: absolute;

      font-size: 1.2em;
      color: $primary;

      content: '/';

      background-image: unset !important;
      transform: translateX(-0.8em);
    }
  }
}
